.about-container {
  width: 100%;
  display: flex;
  overflow: auto;
  min-height: 100vh;
  align-items: center;
  flex-direction: column;
}
.about-header {
  width: 100%;
  height: 72px;
  display: flex;
  padding: var(--dl-space-space-unit);
  z-index: 100;
  position: relative;
  max-width: 100%;
  min-width: 100%;
  align-items: center;
  padding-top: var(--dl-space-space-doubleunit);
  padding-left: var(--dl-space-space-doubleunit);
  padding-right: var(--dl-space-space-doubleunit);
  flex-direction: row;
  padding-bottom: var(--dl-space-space-doubleunit);
  justify-content: space-between;
  background-color: var(--dl-color-gray-black);
}
.about-logo {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.about-navlink {
  display: contents;
}
.about-image {
  width: 42px;
  height: 52px;
  object-fit: cover;
  text-decoration: none;
}
.about-navlink1 {
  color: rgb(255, 255, 255);
  font-size: 24px;
  font-family: PT Sans;
  text-decoration: none;
}
.about-menu {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
}
.about-navlink2 {
  color: rgb(255, 255, 255);
  transition: 0.3s;
  font-family: Fira Sans;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.about-navlink2:hover {
  color: var(--dl-color-turquoise-default);
}
.about-container1 {
