@media(max-width: 991px) {
  .about-container2 {
    display: none;
  }
}
@media(max-width: 767px) {
  .about-menu {
    display: none;
  }
  .about-burger-menu {
    display: block;
  }
  .about-menu2 {
    display: none;
  }
}
@media(max-width: 479px) {
